.mat-form-field+.mat-form-field {
  margin-left: 8px;
}

.mat-expansion-panel {
  margin-bottom: 10px;
  box-shadow: 0px 0px 7px -1px rgba(0, 0, 0, 0.08), 0 1px 1px 0 rgba(0, 0, 0, 0), 0 1px 3px 0 rgba(0, 0, 0, 0) !important;
  border-radius: 10px !important;
}

label {
  display: inline-block;
  margin-bottom: 0;
  color: #3e3e3e;
  font-size: 13px !important;
  font-family: "GothamNarrowBold", sans-serif !important;
  min-width: 116px;
}

#status-toggle .btn-toggle:before {
  content: "off" !important;
  left: -3.5rem !important;
}

#status-toggle .btn-toggle:after {
  content: "On" !important;
  right: -3.5rem !important;
}

#status-toggle .btn-toggle {
  margin: 0 1.5rem !important;
}

textarea {
  resize: none !important;
}

.mat-form-field {
  width: 65% !important;
}

.alert-dark {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 115px;
  margin-top: 16px;
  color: #818181;
  background-color: #f2f2f5;
  border-color: #c6c8ca;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 17px;
  border-radius: 8px;
}

.alert-dark img {
  cursor: pointer;
}

.alert-dismissible {
  padding-right: 10px;
}